@use '../_config/variable' as config_variable;
@use '../_variable_font_icon/interface' as font_icon__interface;
@use '../variable' as variable;
@use '../mixin/button' as mixin_button;

section {
  text-align: center;
}

section:first-of-type {
  img:first-child {
    border: none;
    max-width: 14rem;
  }

  h1 {

    font-size: 6 * variable.$unit_root_1;
    margin-top: 0px;

    // text-shadow: initial;
  }
}

h1, h2, h3, h4, h5, h6 {
  img {
    // max-width: 3rem;
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-top: 1.5em;
    margin-bottom: 0.75em;
  }
}

h2 img {
  max-width: 3em;
}

h3 img {
  max-width: 2em;
}

li img {
  margin-left: auto;
  margin-right: auto;
  display: block;
  margin-top: 1.5em;
  margin-bottom: 0.75em;
  max-width: 3em;
}

.row {
  p, ul {
    text-align: left;
  }
}

// svg {
//   width: 100%;
//   height: auto;
//   max-width: variable.$unit_relative_2 * 16;

//   fill: currentColor;

//   display: inline-block;
// }

main .button {
  @include mixin_button.button-large;
  @include mixin_button.button-icon;

  width: 100%;
  max-width: variable.$unit_relative_1 * 20;
}

#steps {
  list-style: none;

  li {
    padding-left: 0px;
  
    &:not(:last-of-type)::after {
      content: font_icon__interface.$font-icon--interface-arrow_down;
      font-family: 'font_icon_interface';
      // font-size: 1.25 * variable.$unit_relative_2;
    }
  }
}

.row > * {
  & > *:not(h3) {
    display: inline-block;
    max-width: 32 * variable.$unit_root_2;
  }

  p {
    text-align: center;
  }
}
